  .c     { border-left: none;}
  .a     { border-right: none;}

  .a { height: 35px; width:  50px; bottom: 10px; right: 285px;}
  .b { height: 35px; width: 220px; bottom: 10px; right:  60px;}
  .c { height: 35px; width:  50px; bottom: 10px; right:  10px;}
}

.layer-l-1 {
  height: 0px; width: 50px; bottom: 0px; left: 0px; z-index: 2;
  .b { border-image: linear-gradient(180deg, var(--color-2), var(--color-3), var(--color-2)) 1;}

  .c     { border-top: none;}
  .a     { border-bottom: none;}
  .a, .c { border-left: none;}
  .b     { border-right: none;}

  .a { height:  50px; width: 35px; bottom: 585px; left: 10px;}
  .b { height: 520px; width: 35px; bottom:  60px; left: 10px;}
  .c { height:  50px; width: 35px; bottom:  10px; left: 10px;}
